.rocket__item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;
    padding: 15px;
    box-shadow: 0px 0px 86px -46px rgba(0,0,0,0.75);
    span {
        text-transform: uppercase;
    }
}

.rocket-img-wrapper {
    width: 30%;
    height: 220px;
    flex-shrink: 0;
}

.rocket__img {
    width: 100%;
    height: 100%;
}

.rocket__item-info {
    width: 68%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}